}

.App {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  max-width: 100vw;
  background-color: #2b2b2d;
  display: flex;
  align-items: center;
  justify-content: space-around;
  box-sizing: border-box;
}

/* bigshot one font */
.movie {
  width: 320px;
  display: flex;
  flex-direction: column;
}

.movie .wrapper-logo {
  display: flex;
  align-items: flex-end;
}

.movie .wrapper-logo .cinema {
  width: 40px;
  height: 40px;
}

.movie .wrapper-logo p {
  font-size: 20px;
  letter-spacing: 2px;
  color: #F8F8F8;
  margin-left: 5px;
  font-family: 'Pacifico', serif;
  color: #147f92;
}

.movie img {
  margin-top: 50px;
  display: flex;
  width: 100%;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  cursor: pointer;
}

.movie .price {
  font-size: 2em;
  font-weight: 700;
  color: #fff;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.25);
  margin: auto;
  margin-top: 2em;
  background: #348380;
  font-size: 1.1rem;
  width: 100%;
  padding: 0.75em 1.5em;
  border-radius: 3px;
  display: block;
  max-width: 230px;
  font-family: 'Bigshot One', serif;
  letter-spacing: 1px;
  text-align: center;
}

/*==================================================
    media queries
==================================================*/
/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
  .movie {
    width: 90%;
    flex-direction: column;
    display: flex;
    align-items: center;
  }
  .movie .wrapper-logo {
    margin-top: 1em;
  }
  .movie .wrapper-logo .cinema {
    width: 50px;
    height: 50px;
  }
  .movie .wrapper-logo p {
    font-size: 1.4rem;
    margin-left: 5px;
  }
  .movie img {
    margin-top: 1em;
    width: 100%;
    border-radius: 20px;
  }
  .movie .price {
    margin-top: 1em;
    max-width: 230px;
    padding: 0.7em;
    font-size: 1.3rem;
  }
}

/* Small devices (landscape phones, 576px and up)*/
@media (min-width: 576px) and (max-width: 767.98px) {
  .movie {
    width: 80%;
    flex-direction: column;
    align-items: center;
  }
  .movie .wrapper-logo {
    margin-top: 1em;
  }
  .movie .wrapper-logo .cinema {
    width: 50px;
    height: 50px;
  }
  .movie .wrapper-logo p {
    font-size: 1.4rem;
  }
  .movie img {
    margin-top: 2em;
    width: 100%;
    border-radius: 20px;
  }
  .movie .price {
    margin-top: 2em;
    max-width: 230px;
    padding: 1em;
    font-size: 1.3rem;
  }
}

/* Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) {
  .movie {
    width: 80%;
    flex-direction: column;
    border-radius: 50%;
    align-items: center;
  }
  .movie .wrapper-logo {
    margin-top: 1em;
  }
  .movie .wrapper-logo .cinema {
    width: 60px;
    height: 60px;
  }
  .movie .wrapper-logo p {
    font-size: 1.6rem;
  }
  .movie img {
    margin-top: 2em;
    width: 100%;
    border-radius: 20px;
  }
  .movie .price {
    margin-top: 2em;
    max-width: 230px;
    padding: 1em;
    font-size: 1.3rem;
  }
}

/* Large devices (desktops, 992px and up)*/
@media (min-width: 992px) and (max-width: 1499.98px) {
  .movie .wrapper-logo {
    margin-top: -1em;
  }
  .movie .wrapper-logo .cinema {
    width: 40px;
    height: 40px;
  }
  .movie .wrapper-logo p {
    font-size: 20px;
  }
  .movie img {
    margin-top: 1.5em;
    width: 100%;
  }
  .movie .price {
    margin-top: 1em;
    max-width: 230px;
  }
}

/* Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1800px) {
  .movie {
    width: 420px;
  }
  .movie .wrapper-logo {
    margin-top: -2em;
  }
  .movie .wrapper-logo .cinema {
    width: 60px;
    height: 60px;
  }
  .movie .wrapper-logo p {
    font-size: 1.6rem;
  }
  .movie img {
    margin-top: 1.5em;
    width: 100%;
  }
  .movie .price {
    margin-top: 1em;
    max-width: 350px;
    font-size: 1.4rem;
  }
}

