.tooltip {
  position: relative;
  display: inline-block;
}

.tooltip::before,
.tooltip::after {
  position: absolute;
  opacity: 0;
  pointer-events: none;
  left: 50%;
}

.tooltip:hover::before,
.tooltip:hover::after {
  opacity: 1;
  transition: opacity 0.3s ease, transform 0.3s ease;
  transition-delay: 0.1s;
}

.tooltip::before {
  bottom: 100%;
  border: solid transparent;
  content: '';
  height: 0;
  width: 0;
  z-index: 1001;
  pointer-events: none;
  border-color: transparent;
  border-top-color: #57e683;
  border-width: 6px;
  margin-left: -6px;
  transform: translate3d(0, 5px, 0);
}

.tooltip:hover::before {
  transform: translate3d(0, 0, 0);
}

.tooltip::after {
  content: attr(data-tooltip);
  background: #57e683;
  border-radius: 3px;
  color: #fff;
  font-weight: bold;
  z-index: 1000;
  font-size: 14px;
  padding: 8px 10px;
  bottom: 100%;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.3);
  transform: translate3d(-50%, -5px, 0);
}

.tooltip:hover::after {
  transform: translate3d(-50%, -10px, 0);
}

.checkout {
  width: 320px;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  position: relative;
}

.checkout .total, .checkout .action--buy {
  position: absolute;
  font-size: 2em;
  font-weight: 700;
  color: #fff;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.25);
  background: #26AD4F;
  font-size: 1.1rem;
  width: 100%;
  padding: 0.75em 1.5em;
  border-radius: 3px;
  max-width: 230px;
  background: #26AD4F;
  align-self: flex-end;
  cursor: pointer;
  letter-spacing: 2px;
  font-family: 'Bigshot One', serif;
  top: 30em;
}

.checkout .action--buy {
  top: 35em;
}

.checkout .wrapper-basket {
  align-self: flex-end;
  margin-top: 3em;
  margin-bottom: 20em;
  cursor: pointer;
  position: relative;
}

.checkout .wrapper-basket .button-basket {
  cursor: pointer;
  padding: 14px;
  border-radius: 50%;
  position: relative;
}

.checkout .wrapper-basket .button-basket .basket {
  width: 45px;
  height: 45px;
  fill: #eadcd2;
}

.checkout .wrapper-basket .button-basket span {
  width: 22px;
  height: 22px;
  font-size: 16px;
  line-height: 20px;
  color: #eadcd2;
  font-size: 14px;
  font-weight: bold;
  position: absolute;
  border-radius: 12px;
  text-align: center;
  top: 0;
  right: 0;
  bottom: 80%;
  left: 60%;
  margin: auto;
  box-shadow: 1px 2px 2px 2px rgba(8, 112, 184, 0.1);
  -moz-box-shadow: 1px 2px 2px 2px rgba(8, 112, 184, 0.1);
  background: #fb3f4c;
  outline: none;
}

.checkout .wrapper-basket h2 {
  position: absolute;
  top: 0;
  left: 20px;
  color: #ffa500;
}

.checkout .wrapper-basket .tickets {
  position: absolute;
  display: flex;
  top: 60px;
  flex-direction: column;
  left: 20px;
  height: 170px;
  overflow: auto;
  -webkit-animation-name: shift;
          animation-name: shift;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
}

.checkout .wrapper-basket .tickets .wrapper-ticket {
  display: flex;
}

.checkout .wrapper-basket .tickets .wrapper-ticket .cansel-icon {
  width: 20px;
  height: 20px;
  margin-right: 10px;
  margin-bottom: 10px;
}

.checkout .wrapper-basket .tickets .wrapper-ticket .ticket {
  color: #e9d8d8;
  font-size: 18px;
  font-weight: 400;
  letter-spacing: 1px;
  font-family: 'Rowdies', cursive;
  margin-right: 30px;
}

@-webkit-keyframes shift {
  0% {
    opacity: 0;
    overflow: hidden;
  }
  100% {
    opacity: 1;
    overflow: auto;
  }
}

@keyframes shift {
  0% {
    opacity: 0;
    overflow: hidden;
  }
  100% {
    opacity: 1;
    overflow: auto;
  }
}

.animation {
  background: #017374;
  padding-left: 170px;
  padding-bottom: 170px;
  border-radius: 30px;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.25);
  -webkit-animation: move;
          animation: move;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

@-webkit-keyframes move {
  0% {
    padding-left: 0px;
    padding-bottom: 0px;
  }
  100% {
    padding-left: 170px;
    padding-bottom: 170px;
  }
}

@keyframes move {
  0% {
    padding-left: 0px;
    padding-bottom: 0px;
  }
  100% {
    padding-left: 170px;
    padding-bottom: 170px;
  }
}

#style-2::-webkit-scrollbar {
  width: 10px;
  border-radius: 10px;
  background-color: #8d887e;
}

#style-2::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #FFA500;
}

/*==================================================
    media queries
==================================================*/
/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
  .checkout {
    min-height: 75vh;
    width: 320px;
  }
  .checkout .total, .checkout .action--buy {
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    top: 20em;
  }
  .checkout .action--buy {
    top: 25em;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
  }
  .checkout .wrapper-basket {
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
  }
}

/* Small devices (landscape phones, 576px and up)*/
@media (min-width: 576px) and (max-width: 767.98px) {
  .checkout {
    min-height: 70vh;
    width: 50%;
  }
  .checkout .total, .checkout .action--buy {
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    top: 20em;
  }
  .checkout .action--buy {
    top: 25em;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
  }
  .checkout .wrapper-basket {
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
  }
}

/* Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) {
  .checkout {
    min-height: 70vh;
  }
  .checkout .total, .checkout .action--buy {
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    top: 20em;
  }
  .checkout .action--buy {
    top: 25em;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
  }
  .checkout .wrapper-basket {
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
  }
}

/* Large devices (desktops, 992px and up)*/
@media (min-width: 992px) and (max-width: 1499.98px) {
  .checkout {
    min-height: 100vh;
  }
  .checkout .total, .checkout .action--buy {
    top: 32em;
  }
  .checkout .action--buy {
    top: 37em;
  }
}

/* Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1800px) {
  .checkout {
    min-height: 100vh;
  }
  .checkout .total, .checkout .action--buy {
    top: 40em;
    padding: 1em 2em;
  }
  .checkout .action--buy {
    top: 45em;
    padding: 1em 2em;
  }
  .checkout .wrapper-basket .button-basket {
    padding: 20px;
  }
  .checkout .wrapper-basket .button-basket span {
    width: 25px;
    height: 25px;
    bottom: 80%;
    font-size: 0.9rem;
  }
}

